.outer {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: black;
  align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px solid #ffbb00;
  padding: 10px;
  border-radius: 10px;
}

form input {
  width: 350px;
  padding: 5px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  color: black;
  border: none;
}
form select {
  width: 350px;
  padding: 10px;
  font-size: 17px;
  border-radius: 10px;
  margin: 10px;
  color: black;
  border: none;
  border-bottom: 2px solid #ffbb00;
}

div button {
  font-family: arial;
  color: #fbfaf7 !important;
  font-size: 20px;
  text-shadow: 1px 1px 0px #fcae38;
  box-shadow: 1px 1px 1px #fe9902;
  padding: 10px 25px;
  border-radius: 10px;
  border: 2px solid #c2924c;
  background: #ffc56e;
  background: linear-gradient(to top, #ffc56e, #c2924c);
}
button:hover {
  color: #d17f04 !important;
  background: #468ccf;
  background: linear-gradient(to top, #c2924c, #ffc56e);
}

label {
  font-size: 20px;
  color: White;
}
